/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 20px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 12px;
    width: 12px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #A729F5;
  }
  
  /* input:focus + .slider {
    box-shadow: 0 0 1px #A729F5;
  } */
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(10px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
@media only screen and (min-width:768px){
  .switch{
    width: 48px;
    height: 28px;
  }
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 6px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  input:checked + .slider:before {
    -webkit-transform: translateX(36px);
    -ms-transform: translateX(26px);
    transform: translateX(16px);
  }
  .dark-sun{
    width: 24px;
    height: 24px;
  }
  .dark-moon{
      width: 24px;
      height: 24px;
  }
  .light-sun{
      width: 24px;
      height: 24px;
  }
  .light-moon{
      width: 24px;
      height: 24px;
  }
  .toggleContainer{
    gap:16px;
   }
}
@media only screen and (min-width:1440px){
  .toggleContainer{
    position:relative;
  }
}